.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  width: 60%;
  height: 60%;
  /* object-fit: cover; */
  position: absolute;
  padding: auto auto;
  /* margin-top: 25%;
  margin-left: 20%; */
  top: 17%;
  right: 15%;
  z-index: 100;
  border-radius: 50%;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: rgb(85, 85, 161);
  /* #59b256; */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

.mobile-nav {
  display: none;
}

@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }

  .i-left-wrapper {
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .i-desc {
    display: none;
  }

  .mobile-nav {
    background-color: #59b256;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 35vh;
    width: 100vw;
    position: absolute;
  }

  .item {
    padding: 10px;
  }
}

@media (min-width: 500px) and (max-width: 1000px) {
  /* .i-img {
    display: none;
  } */

  .i {
    display: flex;
    height: 50vh;
  }
}
